import React from "react";
import axios from "axios";

class ApiRequest extends React.Component {
  constructor() {
    super();
    this.state = {
      arr: []
    };
  }

  // lifecycle
  componentDidMount() {
    axios.get("https://615b9a964a360f0017a8168b.mockapi.io/api/student").then(
      (posRes) => {
        this.setState({ arr: posRes.data });
      },
      (errRes) => {
        console.log(errRes);
      }
    );
  }
  render() {
    return (
      <div>
        <table border="1" align="center">
          <thead>
            <tr>
              <th>Sno.</th>
              <th> Name</th>
              <th>Age </th>
            </tr>
          </thead>
          <tbody>
            {this.state.arr.map((el, i) => (
              <tr key={i}>
                <td>{i} </td>
                <td>{el.Name} </td>
                <td>{el.Age} </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }
}
export default ApiRequest;
